//parent
button,
input[type="button"],
input[type="reset"] {
	border: 1px solid;
	border-color: #ccc #ccc #bbb;
	border-radius: 2px;
	background: #e6e6e6;
//	box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.5), inset 0 15px 17px rgba(255, 255, 255, 0.5), inset 0 -5px 12px rgba(0, 0, 0, 0.05);
	color: rgba(0, 0, 0, .8);
	font-size: 12px;
	font-size: 0.75em;
	line-height: 1;
	padding: .6em 1em .4em;
//	text-shadow: 0 1px 0 rgba(255, 255, 255, 0.8);
}
input {
	max-width: 100%;
}

